@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo {
    width: 100%;
    height: 150px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.logoimg {
    background-image: url("/PWA-Dictionary/static/media/D. logo nobg.24336b84.png");
    background-size: 100% 100%;
    width: 167px;
    height: 167px;
}

h1 {
    font-family: "Inter", sans-serif;
    font-size: 36pt;
    color: #969696;
    font-weight: 600;
}

@media (max-width: 500px) {
    .logo {
        background-color: #ACC3D4;
        border-radius: 15px 15px 0px 0px;
        height: 100px;
    }
    .logoimg {
        display: none;
    }
    h1 {
        font-size: 30pt;
        color: #FFFFFF;
    }
}
  
.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-bottom: 1.5vh;
}

.leftbox {
  width: 48%;
  background-color: #ACC3D4;
  height: 193px;
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border-radius: 15px 15px 0px 15px;
}

.rightbox {
  width: 48%;
  height: 193px;
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border: 1px solid #D0D0D0;
  border-radius: 15px 15px 15px 0px;
}

.input {
  width: 100%;
}

.search{
  width: 100%;
  color: #969696;
  background-color: #fff;
  border-radius: 30px;
  outline: none;
  font-size: 6vw;
  text-transform: uppercase;
  font-family: "Inter", sans-serif;
}
.select {
  color: #969696;
  background-color: #fff;
  border-radius: 30px;
}

@media (max-width: 500px) {
  .header {
    height: 25vh;
    flex-direction: column;
  }

  .leftbox {
    width: 100%;
    border-radius: 0px 0px 0px 15px;
    margin-bottom: 2vh;
  }
  
  .rightbox {
    width: 100%;
  }
}

.meanings {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  scrollbar-width: thin;
  height: 55vh;
  border: 1px solid #D0D0D0;
  border-radius: 23px;
  padding: 10px 20px;
  overflow-x: hidden;
}

.subTitle {
  font-size: 5vw;
  font-family: "Inter", sans-serif;
  color: #969696;
}

.single-meaning {
  border: 1px solid #D0D0D0;
  display: flex;
  flex-direction: column;
  border-radius: 23px;
  padding: 10px 20px;
  margin: 10px 0;
  font-family: "Inter", sans-serif;
  color: #969696;
  font-weight: 100;
}


